.script-generator {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: Arial, sans-serif;
  padding: 20px;
  box-sizing: border-box;
}
.script-generator__input-wrapper, .script-generator__content {
  width: 100%;
}
.script-generator__button:hover {
  background-color: #3e8e41;
}
.script-generator__button:hover:hover {
  cursor: pointer;
}

.input-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

textarea {
  width: 100%;
  height: 50vh;
  font-size: 16px;
  line-height: 1.5;
  padding: 10px;
  border: 2px solid #ccc;
  border-radius: 5px;
  resize: vertical;
  box-sizing: border-box;
}

.button-wrapper {
  margin-top: 20px;
}

.button {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 5px;
  box-sizing: border-box;
  transition: background-color 0.3s;
}

.button:hover {
  background-color: #3e8e41;
}

.output-wrapper {
  width: 100%;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.output-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.output-text {
  width: 100%;
  font-size: 16px;
  line-height: 1.5;
  padding: 10px;
  border: 2px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  white-space: pre-wrap;
}

